* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-form {
  font: inherit;
  max-width: 320px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 10px 20px;
  font-size: 24px;
  display: flex;
}

.search-form input, .search-form button {
  border: none;
  border-radius: 5px;
  margin: 0 10px;
  padding: 10px 24px;
  font-size: 16px;
}

.search-form input {
  max-width: 400px;
  flex: 1;
}

.search-form button {
  cursor: pointer;
  transition: all .2s ease-in-out;
}

.search-form-container {
  background-color: #4a61bd;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(4, 1fr);
  display: grid;
}

.photo-card {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.photo-card img {
  max-width: 100%;
  height: auto;
}

.info {
  text-align: center;
  margin-top: 10px;
}

.info-item {
  margin: 5px 0;
  font-size: 14px;
  line-height: 1.5;
}

.is-hidden {
  display: none;
}

.load-more {
  color: #fff;
  background-color: #4a60bd;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 20px;
  display: block;
}

